import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from '../app/home/home.component';
import { InfoComponent } from '../app/info/info.component';
import { StandingsComponent } from '../app/standings/standings.component';
import { GalleryComponent } from '../app/gallery/gallery.component';
import { EntriesComponent } from '../app/entries/entries.component';
import { PublicInfoComponent } from 'src/app/info/public-info/public-info.component';
import { LapTimesComponent } from 'src/app/info/lap-times/lap-times.component';
import { ArchivesComponent } from 'src/app/gallery/archives/archives.component';
import { AshleyComponent } from 'src/app/gallery/ashley/ashley.component';
import { PitbabesComponent } from 'src/app/gallery/pitbabes/pitbabes.component';
import { VideosComponent } from 'src/app/gallery/videos/videos.component';
import { TeamBuildingComponent } from 'src/app/team-building/team-building.component';
import { TeamBuildingPicturesComponent } from 'src/app/team-building/team-building-pictures/team-building-pictures.component';

const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'home', component: HomeComponent },
  { path: 'member-info', component: InfoComponent },
  { path: 'public-info', component: PublicInfoComponent },
  { path: 'lap-time', component: LapTimesComponent },
  { path: 'regional-standings', component: StandingsComponent },
  { path: 'entries', component: EntriesComponent },
  { path: 'gallery', component: GalleryComponent },
  { path: 'archives', component: ArchivesComponent },
  { path: 'ashley-mills', component: AshleyComponent },
  { path: 'videos', component: VideosComponent },
  { path: 'pitbabes', component: PitbabesComponent },
  { path: 'action-netval-2014', component: TeamBuildingComponent },
  { path: 'pictures-action-netbal', component: TeamBuildingPicturesComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {}
